import React, { Suspense, lazy } from "react";
import {Routes, Route, Link, Navigate, RouterProvider, createBrowserRouter, createRoutesFromElements} from 'react-router-dom'
const Auth = lazy(() => import('./Auth/Auth'))
const FirstPage = lazy(() => import('./FirstPage/FirstPage'))

const router = createBrowserRouter(createRoutesFromElements(
    <Route path="/" >
        <Route index  element={<Suspense fallback={<h1>Loading...</h1>}><Auth /></Suspense>}/>
        <Route path="FirstPage" element={<Suspense fallback={<h1>Loading...</h1>}><FirstPage /></Suspense>}/>
    </Route>
))

const Main = () => {
    return(
        <div className="main">
            <RouterProvider router={router} />
        </div>
    )
}

export {Main}